<template>
  <div class="showview">
    <BlogItem v-for="item in listArr" :key="item.id" :item="item"></BlogItem>
    <div class="pageGroup">
      <div class="btn btnLeft" @click="btnLeft">上一页</div>
      <div class="btn btnRight" @click="btnRight">下一页</div>
      <div class="toase" v-show="showToase">数据正在加载......</div>
    </div>
  </div>
</template>

<script>
import BlogItem from './BlogItem'
// import axios from "axios";
export default {
  data () {
    return {
      num: 9,
      page: 1,
      listArr: [],
      showToase: false
    }
  },
  components: {
    BlogItem
  },
  created () {
    this.getData()
  },
  methods: {
    // 点击左边按钮
    btnLeft () {
      if (this.page <= 1) {
        this.page = 1
        return
      }
      this.showToase = true
      this.page--
      this.getData()
      this.scrTop()
    },
    // 右边按钮
    btnRight () {
      if (this.listArr.length < this.num || this.listArr.length == 0) {
        return
      }
      this.showToase = true
      this.page++
      this.getData()
      this.scrTop()
    },
    // 回到页面顶端
    scrTop () {
      if (document.body.scrollTop) {
        document.body.scrollTo = 0
      } else {
        document.documentElement.scrollTop = 0
      }
    },
    // 获取请求
    getData () {
      this.axios({
        url: 'https://ku.qingnian8.com/dataApi/blog/showBlog.php',
        params: {
          page: this.page,
          num: this.num
        }
      }).then((res) => {
        this.listArr = res.data
        this.showToase = false
      })
    }
  }
}
</script>

<style lang="less" scoped>
.toase {
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  color: #fff;
  font-size: 30px;
  z-index: 99;
}
.showview {
  padding: 30px;
}
.pageGroup {
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  .btn {
    border: 1px solid #41b883;
    border-radius: 5px;
    padding: 10px 20px;
    color: #41b883;
    font-size: 18px;
    margin: 0 10px;
    cursor: pointer;
    transition: 0.3s;
    &:hover {
      background: #41b883;
      color: #fff;
    }
  }
}
</style>
